// src/features/line-dashboard/components/data-table/hooks/useQuickFilters.js
"use client"

import * as React from "react"

import {
  applyQuickFilters,
  countActiveQuickFilters,
  createInitialQuickFilters,
  createQuickFilterSections,
  isMultiSelectFilter,
  syncQuickFiltersToSections,
} from "../filters/quickFilters"

/**
 * ÌÄµ ÌïÑÌÑ∞ ÏÑπÏÖò ÏÉÅÌÉúÎ•º ÏÉùÏÑ±¬∑Ïú†ÏßÄÌïòÍ≥†, Ï†ÅÏö©Îêú ÌïÑÌÑ∞Ïóê ÎßûÎäî Ìñâ Î™©Î°ùÏùÑ ÎèåÎ†§Ï£ºÎäî ÌõÖÏûÖÎãàÎã§.
 */
export function useQuickFilters(columns, rows) {
  // Ïª¨Îüº/Ìñâ Îç∞Ïù¥ÌÑ∞Î•º Í∏∞Î∞òÏúºÎ°ú Ïñ¥Îñ§ ÌÄµ ÌïÑÌÑ∞ ÏÑπÏÖòÏù¥ ÌïÑÏöîÌïúÏßÄ Í≥ÑÏÇ∞Ìï©ÎãàÎã§.
  const sections = React.useMemo(
    () => createQuickFilterSections(columns, rows),
    [columns, rows]
  )

  // ÌïÑÌÑ∞ ÏÉÅÌÉúÎäî ÏÑπÏÖò Íµ¨Ï°∞Ïóê ÎßûÏ∂∞ Í∏∞Î≥∏Í∞íÏùÑ ÏÉùÏÑ±Ìï¥ Îë°ÎãàÎã§.
  const [filters, setFilters] = React.useState(() => createInitialQuickFilters())

  // Ïª¨ÎüºÏù¥ Î∞îÎÄåÎ©¥ ÏÑπÏÖòÎèÑ Î∞îÎÄåÎØÄÎ°ú, Í∏∞Ï°¥ ÏÉÅÌÉúÎ•º ÏÉà Íµ¨Ï°∞Ïóê ÎßûÏ∂∞ Ï†ïÎ¶¨Ìï©ÎãàÎã§.
  React.useEffect(() => {
    setFilters((previous) => syncQuickFiltersToSections(previous, sections))
  }, [sections])

  // Ïã§Ï†úÎ°ú ÌÄµ ÌïÑÌÑ∞Î•º Ï†ÅÏö©Ìïú Ìñâ Î™©Î°ùÏûÖÎãàÎã§.
  const filteredRows = React.useMemo(
    () => applyQuickFilters(rows, sections, filters),
    [rows, sections, filters]
  )

  // ÌòÑÏû¨ Î™á Í∞úÏùò ÌïÑÌÑ∞Í∞Ä ÌôúÏÑ±ÌôîÎêòÏñ¥ ÏûàÎäîÏßÄ Ïπ¥Ïö¥Ìä∏Ìï©ÎãàÎã§.
  const activeCount = React.useMemo(
    () => countActiveQuickFilters(filters),
    [filters]
  )

  // Îã®Ïùº ÏÑ†ÌÉù/Îã§Ï§ë ÏÑ†ÌÉù ÌïÑÌÑ∞Î•º Íµ¨Î∂ÑÌïòÏó¨ ÌÜ†Í∏Ä ÎèôÏûëÏùÑ Ï†ïÏùòÌï©ÎãàÎã§.
  const toggleFilter = React.useCallback((key, value) => {
    setFilters((previous) => {
      const isMulti = isMultiSelectFilter(key)
      if (value === null) {
        return { ...previous, [key]: isMulti ? [] : null }
      }

      if (!isMulti) {
        return { ...previous, [key]: previous[key] === value ? null : value }
      }

      const currentValues = Array.isArray(previous[key]) ? previous[key] : []
      const exists = currentValues.includes(value)
      const nextValues = exists
        ? currentValues.filter((item) => item !== value)
        : [...currentValues, value]

      return { ...previous, [key]: nextValues }
    })
  }, [])

  const resetFilters = React.useCallback(
    () => setFilters(createInitialQuickFilters()),
    []
  )

  return {
    sections,
    filters,
    filteredRows,
    activeCount,
    toggleFilter,
    resetFilters,
  }
}
