/**
 * DataTable.jsx (React 19 ÏµúÏ†ÅÌôî Î≤ÑÏ†Ñ)
 * ---------------------------------------------------------------------------
 * ‚úÖ ÌïµÏã¨
 * 1) "ÌòÑÏû¨ Î≥¥Ïù¥Îäî Îç∞Ïù¥ÌÑ∞(ÌïÑÌÑ∞ Î∞òÏòÅ filteredRows)" Í∏∞Ï§ÄÏúºÎ°ú process_flow / comment ÏûêÎèôÌè≠ Í≥ÑÏÇ∞
 * 2) <colgroup> + TH/TD width ÎèôÍ∏∞Ìôî ‚áí Ïª¨Îüº Ï†ÑÏ≤¥ Ìè≠Ïù¥ ÏùºÍ¥ÄÎêòÍ≤å Î≥ÄÌï®
 * 3) TanStack Table v8: Ï†ïÎ†¨/Í≤ÄÏÉâ/Ïª¨Îüº ÏÇ¨Ïù¥Ïßï/ÌéòÏù¥ÏßÄÎÑ§Ïù¥ÏÖò/ÌÄµÌïÑÌÑ∞ Í∑∏ÎåÄÎ°ú Ïú†ÏßÄ
 * 4) React 19: useMemo/useCallback ÏµúÏÜåÌôî (ÌïÑÏöîÌïú ÏßÄÏ†êÎßå ÏÇ¨Ïö©)
 *
 * ‚ö†Ô∏è ÌåÅ
 * - auto width Í≥ÑÏÇ∞ÏùÄ column-defs.jsx ÎÇ¥Î∂ÄÏùò createColumnDefsÍ∞Ä Îã¥ÎãπÌï©ÎãàÎã§.
 *   Ïó¨Í∏∞ÏÑúÎäî Í∑∏ÎïåÍ∑∏Îïå "filteredRows"Î•º rowsForSizingÏúºÎ°ú ÎÑòÍ≤®Ï£ºÎ©¥ ÎÅù!
 */

import * as React from "react"
import {
  flexRender,
  getCoreRowModel,
  getFilteredRowModel,
  getPaginationRowModel,
  getSortedRowModel,
  useReactTable,
} from "@tanstack/react-table"
import {
  IconChevronDown,
  IconChevronLeft,
  IconChevronRight,
  IconChevronUp,
  IconChevronsLeft,
  IconChevronsRight,
  IconDatabase,
  IconRefresh,
} from "@tabler/icons-react"

import { cn } from "@/lib/utils"
import { Button } from "@/components/ui/button"
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"

import { createColumnDefs } from "./column-defs"
import { createGlobalFilterFn } from "./filters/GlobalFilter"
import { QuickFilters } from "./filters/QuickFilters"
import { useDataTableState } from "./hooks/useDataTable"
import { useQuickFilters } from "./hooks/useQuickFilters"
import { numberFormatter, timeFormatter } from "./utils/constants"
import {
  getJustifyClass,
  getTextAlignClass,
  isNullishDisplay,
  resolveCellAlignment,
  resolveHeaderAlignment,
} from "./utils/table"

/* ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
 * 1) ÎùºÎ≤®/Î¨∏Íµ¨ ÏÉÅÏàò
 * ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */
const EMPTY = {
  text: "",
  loading: "Loading rows‚Ä¶",
  noRows: "No rows returned.",
  noMatches: "No rows match your filter.",
}

const LABELS = {
  titleSuffix: "Line E-SOP Status",
  updated: "Updated",
  refresh: "Refresh",
  showing: "Showing",
  rows: "rows",
  filteredFrom: " (filtered from ",
  filteredFromSuffix: ")",
  rowsPerPage: "Rows per page",
  page: "Page",
  of: "of",
  goFirst: "Go to first page",
  goPrev: "Go to previous page",
  goNext: "Go to next page",
  goLast: "Go to last page",
}

/**
 * @param {{ lineId: string }} props
 */
export function DataTable({ lineId }) {
  /* ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
   * 2) Îç∞Ïù¥ÌÑ∞/ÏÉÅÌÉú ÌõÖ
   *    - rows: ÏÑúÎ≤Ñ/ÏøºÎ¶¨Î°ú Í∞ÄÏ†∏Ïò® ÏõêÎ≥∏ Îç∞Ïù¥ÌÑ∞
   *    - filteredRows: QuickFilters + GlobalFilter Ï†ÅÏö©Îêú "ÌòÑÏû¨ Î≥¥Ïù¥Îäî" Îç∞Ïù¥ÌÑ∞
   * ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */
  const {
    columns,
    rows,
    filter,
    setFilter,
    sorting,
    setSorting,
    isLoadingRows,
    rowsError,
    fetchRows,
    tableMeta,
  } = useDataTableState({ lineId })

  const { sections, filters, filteredRows, activeCount, toggleFilter, resetFilters } =
    useQuickFilters(columns, rows)

  /* ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
   * 3) React 19 Ïä§ÌÉÄÏùº: ÌïÑÏöîÌïú ÏßÄÏ†êÎßå useMemo
   *    - ÏûêÎèôÌè≠ Í≥ÑÏÇ∞Ïùò Í∏∞Ï§ÄÏùÄ "ÌòÑÏû¨ Î≥¥Ïù¥Îäî Îç∞Ïù¥ÌÑ∞"Ïó¨Ïïº Ï≤¥Í∞êÏù¥ Ï¢ãÏäµÎãàÎã§.
   * ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */
  const firstVisibleRow = filteredRows[0]

  // ‚úÖ Ïª¨Îüº Ï†ïÏùò: filteredRowsÎ•º rowsForSizingÏúºÎ°ú ÎÑòÍ≤® "ÌòÑÏû¨ Î≥¥Ïù¥Îäî Îç∞Ïù¥ÌÑ∞ Í∏∞Ï§Ä ÏûêÎèôÌè≠" Ïã§ÌòÑ
  const columnDefs = React.useMemo(
    () => createColumnDefs(columns, undefined, firstVisibleRow, filteredRows),
    [columns, firstVisibleRow, filteredRows]
  )

  // Í∏ÄÎ°úÎ≤å ÌïÑÌÑ∞ Ìï®Ïàò: Ïª¨Îüº Ïä§ÌÇ§ÎßàÍ∞Ä Î∞îÎÄî ÎïåÎßå Ïû¨ÏÉùÏÑ±
  const globalFilterFn = React.useMemo(
    () => createGlobalFilterFn(columns),
    [columns]
  )

  /* ÌéòÏù¥ÏßÄÎÑ§Ïù¥ÏÖò/Ïª¨Îüº ÏÇ¨Ïù¥Ïßï Î°úÏª¨ ÏÉÅÌÉú */
  const [pagination, setPagination] = React.useState({ pageIndex: 0, pageSize: 15 })
  const [columnSizing, setColumnSizing] = React.useState({})

  /* TanStack Table Ïù∏Ïä§ÌÑ¥Ïä§ */
  /* eslint-disable react-hooks/incompatible-library */
  const table = useReactTable({
    data: filteredRows,               // ‚úÖ Î≥¥Ïù¥Îäî Îç∞Ïù¥ÌÑ∞Î°ú ÌÖåÏù¥Î∏î Íµ¨ÏÑ±
    columns: columnDefs,              // ‚úÖ ÎèôÏ†Å Ìè≠ Î∞òÏòÅÎêú Ïª¨Îüº Ï†ïÏùò
    meta: tableMeta,
    state: {
      sorting,
      globalFilter: filter,
      pagination,
      columnSizing,
    },
    onSortingChange: setSorting,
    onGlobalFilterChange: setFilter,
    onPaginationChange: setPagination,
    onColumnSizingChange: setColumnSizing,
    globalFilterFn,

    // Row models
    getCoreRowModel: getCoreRowModel(),
    getFilteredRowModel: getFilteredRowModel(),
    getSortedRowModel: getSortedRowModel(),
    getPaginationRowModel: getPaginationRowModel(),

    // ÎìúÎûòÍ∑∏ Ï§ë Ïã§ÏãúÍ∞Ñ Î¶¨ÏÇ¨Ïù¥Ï¶à Î∞òÏòÅ
    columnResizeMode: "onChange",
  })
  /* eslint-enable react-hooks/incompatible-library */

  /* ÌååÏÉù Í∞í(Î†åÎçî Ìé∏Ïùò) */
  const emptyStateColSpan = Math.max(table.getVisibleLeafColumns().length, 1)
  const totalLoaded = rows.length
  const filteredTotal = filteredRows.length
  const hasNoRows = !isLoadingRows && rowsError === null && columns.length === 0

  const currentPage = pagination.pageIndex + 1
  const totalPages = Math.max(table.getPageCount(), 1)
  const currentPageSize = table.getRowModel().rows.length

  /* ÏÉÅÎã® "Updated ..." ÎùºÎ≤® */
  const [lastUpdatedLabel, setLastUpdatedLabel] = React.useState(null)

  /* ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
   * 4) Effects
   * ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */
  // Î°úÎî©Ïù¥ ÎÅùÎÇòÎ©¥ "ÎßàÏßÄÎßâ Í∞±Ïã† ÏãúÍ∞Å" ÏóÖÎç∞Ïù¥Ìä∏
  React.useEffect(() => {
    if (isLoadingRows) return
    setLastUpdatedLabel(timeFormatter.format(new Date()))
  }, [isLoadingRows])

  // ÌïÑÌÑ∞/Ï†ïÎ†¨/ÌÄµÌïÑÌÑ∞Í∞Ä Î∞îÎÄåÎ©¥ 1ÌéòÏù¥ÏßÄÎ°ú Î¶¨ÏÖã
  React.useEffect(() => {
    setPagination((prev) => (prev.pageIndex === 0 ? prev : { ...prev, pageIndex: 0 }))
  }, [filter, sorting, filters])

  // ÌéòÏù¥ÏßÄ Ïàò Í∞êÏÜå Ïãú pageIndex Î≥¥Ï†ï
  React.useEffect(() => {
    const maxIndex = Math.max(table.getPageCount() - 1, 0)
    setPagination((prev) => (prev.pageIndex > maxIndex ? { ...prev, pageIndex: maxIndex } : prev))
  }, [table, rows.length, filteredRows.length, pagination.pageSize])

  /* ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
   * 5) Ïù¥Î≤§Ìä∏ Ìï∏Îì§Îü¨
   * ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */
  function handleRefresh() {
    void fetchRows()
  }

  /* ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
   * 6) ÌÖåÏù¥Î∏î Î∞îÎîî Î†åÎçî
   *    - ÏÉÅÌÉúÎ≥Ñ Î∂ÑÍ∏∞: Î°úÎî© ‚Üí ÏóêÎü¨ ‚Üí Ïä§ÌÇ§Îßà ÏóÜÏùå ‚Üí ÌïÑÌÑ∞ Í≤∞Í≥º ÏóÜÏùå ‚Üí ÏùºÎ∞ò Ìñâ
   *    - TH/TDÏóê width/min/maxÎ•º "px Î¨∏ÏûêÏó¥"Î°ú ÏßÄÏ†ïÌï¥ colgroupÍ≥º ÏùºÍ¥Ä ÎèôÏûë
   * ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */
  function renderTableBody() {
    if (isLoadingRows) {
      return (
        <TableRow>
          <TableCell
            colSpan={emptyStateColSpan}
            className="h-26 text-center text-sm text-muted-foreground"
            aria-live="polite"
          >
            {EMPTY.loading}
          </TableCell>
        </TableRow>
      )
    }
    if (rowsError) {
      return (
        <TableRow>
          <TableCell
            colSpan={emptyStateColSpan}
            className="h-26 text-center text-sm text-destructive"
            role="alert"
          >
            {rowsError}
          </TableCell>
        </TableRow>
      )
    }
    if (hasNoRows) {
      return (
        <TableRow>
          <TableCell
            colSpan={emptyStateColSpan}
            className="h-26 text-center text-sm text-muted-foreground"
            aria-live="polite"
          >
            {EMPTY.noRows}
          </TableCell>
        </TableRow>
      )
    }

    const visibleRows = table.getRowModel().rows
    if (visibleRows.length === 0) {
      return (
        <TableRow>
          <TableCell
            colSpan={emptyStateColSpan}
            className="h-26 text-center text-sm text-muted-foreground"
            aria-live="polite"
          >
            {EMPTY.noMatches}
          </TableCell>
        </TableRow>
      )
    }

    return visibleRows.map((row) => (
      <TableRow key={row.id}>
        {row.getVisibleCells().map((cell) => {
          const isEditable = Boolean(cell.column.columnDef.meta?.isEditable)
          const align = resolveCellAlignment(cell.column.columnDef.meta) // "left" | "center" | "right"
          const textAlignClass = getTextAlignClass(align)
          const width = cell.column.getSize()
          const widthPx = `${width}px`

          const raw = cell.getValue()
          const content = isNullishDisplay(raw)
            ? EMPTY.text
            : flexRender(cell.column.columnDef.cell, cell.getContext())

          return (
            <TableCell
              key={cell.id}
              data-editable={isEditable ? "true" : "false"}
              style={{ width: widthPx, minWidth: widthPx, maxWidth: widthPx }}
              className={cn(
                "align-center",
                textAlignClass,
                !isEditable && "caret-transparent focus:outline-none"
              )}
            >
              {/* ÎÇ¥Î∂ÄÎäî Ìè≠ ÏßÄÏ†ï ÏóÜÏù¥ ÌÖçÏä§Ìä∏ Ïò§Î≤ÑÌîåÎ°ú Ï≤òÎ¶¨ */}
              <div className="truncate">{content}</div>
            </TableCell>
          )
        })}
      </TableRow>
    ))
  }

  /* ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
   * 7) Î†åÎçî
   *    - table-fixed + colgroup: Ïª¨Îüº Îã®ÏúÑ Ìè≠Ïù¥ ÌôïÏã§Ìûà Ï†ÅÏö©
   *    - Table Ï†ÑÏ≤¥ widthÎäî table.getTotalSize()Î°ú ÏßÄÏ†ï (px Î¨∏ÏûêÏó¥)
   * ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */
  return (
    <section className="flex h-full min-h-0 min-w-0 flex-col gap-3 px-4 lg:px-6">
      {/* ÏÉÅÎã®: ÌÉÄÏù¥ÌãÄ/Î¶¨ÌîÑÎ†àÏãú */}
      <div className="flex flex-wrap justify-between items-start">
        <div className="flex flex-col gap-1">
          <div className="flex items-center gap-2 text-lg font-semibold">
            <IconDatabase className="size-5" />
            {lineId} {LABELS.titleSuffix}
            <span className="ml-2 text-[10px] font-normal text-muted-foreground self-end" aria-live="polite">
              {LABELS.updated} {lastUpdatedLabel || "-"}
            </span>
          </div>
        </div>

        <div className="flex items-center gap-2 self-end mr-3">
          <Button
            variant="outline"
            size="sm"
            onClick={handleRefresh}
            className="gap-1"
            aria-label={LABELS.refresh}
            title={LABELS.refresh}
          >
            <IconRefresh className="size-3" />
            {LABELS.refresh}
          </Button>
        </div>
      </div>

      {/* ÌÄµ ÌïÑÌÑ∞ */}
      <QuickFilters
        sections={sections}
        filters={filters}
        activeCount={activeCount}
        onToggle={toggleFilter}
        onClear={resetFilters}
        globalFilterValue={filter}
        onGlobalFilterChange={setFilter}
      />

      {/* ÌÖåÏù¥Î∏î */}
      <TableContainer className="flex-1 h-[calc(100vh-3rem)] overflow-y-auto overflow-x-auto rounded-lg border px-1">
        <Table
          className="table-fixed w-full"
          style={{ width: `${table.getTotalSize()}px`, tableLayout: "fixed" }}
          stickyHeader
        >
          {/* ‚úÖ Ïª¨Îüº Ï†ÑÏ≤¥ Ìè≠ ÎèôÍ∏∞Ìôî: colgroupÏóê getVisibleLeafColumns() ÏÇ¨Ïù¥Ï¶àÎ•º Î∞òÏòÅ */}
          <colgroup>
            {table.getVisibleLeafColumns().map((column) => (
              <col key={column.id} style={{ width: `${column.getSize()}px` }} />
            ))}
          </colgroup>

          <TableHeader>
            {table.getHeaderGroups().map((headerGroup) => (
              <TableRow key={headerGroup.id}>
                {headerGroup.headers.map((header) => {
                  const canSort = header.column.getCanSort()
                  const sortDirection = header.column.getIsSorted() // "asc" | "desc" | false
                  const meta = header.column.columnDef.meta
                  const align = resolveHeaderAlignment(meta)
                  const justifyClass = getJustifyClass(align)
                  const headerContent = flexRender(header.column.columnDef.header, header.getContext())
                  const width = header.getSize()
                  const widthPx = `${width}px`

                  const ariaSort =
                    sortDirection === "asc"
                      ? "ascending"
                      : sortDirection === "desc"
                        ? "descending"
                        : "none"

                  return (
                    <TableHead
                      key={header.id}
                      className={cn("relative whitespace-nowrap sticky top-0 z-10 bg-muted")}
                      style={{ width: widthPx, minWidth: widthPx, maxWidth: widthPx }}
                      scope="col"
                      aria-sort={ariaSort}
                    >
                      {canSort ? (
                        <button
                          className={cn("flex w-full items-center gap-1", justifyClass)}
                          onClick={header.column.getToggleSortingHandler()}
                          aria-label={`Sort by ${String(header.column.id)}`}
                        >
                          {headerContent}
                          {sortDirection === "asc" && <IconChevronUp className="size-4" />}
                          {sortDirection === "desc" && <IconChevronDown className="size-4" />}
                        </button>
                      ) : (
                        <div className={cn("flex w-full items-center gap-1", justifyClass)}>
                          {headerContent}
                        </div>
                      )}

                      {/* Ïª¨Îüº Î¶¨ÏÇ¨Ïù¥Ï†Ä (ÏãúÍ∞ÅÏ†Å Ìï∏Îì§) */}
                      <span
                        onMouseDown={header.getResizeHandler()}
                        onTouchStart={header.getResizeHandler()}
                        className="absolute right-0 top-0 h-full w-1 cursor-col-resize select-none touch-none"
                        role="separator"
                        aria-orientation="vertical"
                        aria-label={`Resize column ${String(header.column.id)}`}
                        tabIndex={-1}
                      />
                    </TableHead>
                  )
                })}
              </TableRow>
            ))}
          </TableHeader>

          <TableBody>{renderTableBody()}</TableBody>
        </Table>
      </TableContainer>

      {/* ÌïòÎã®: ÏöîÏïΩ/ÌéòÏù¥ÏßÄÎÑ§Ïù¥ÏÖò */}
      <div className="flex flex-col gap-2 lg:flex-row lg:items-center lg:justify-between">
        <div className="flex flex-wrap items-center gap-2 text-xs text-muted-foreground">
          <span aria-live="polite">
            {LABELS.showing} {numberFormatter.format(currentPageSize)} {LABELS.rows}
            {" of "} {numberFormatter.format(filteredTotal)} {LABELS.rows}
            {filteredTotal !== totalLoaded
              ? `${LABELS.filteredFrom}${numberFormatter.format(totalLoaded)}${LABELS.filteredFromSuffix}`
              : ""}
          </span>
        </div>

        <div className="flex flex-col gap-2 sm:flex-row sm:items-center sm:justify-end">
          <div className="flex items-center gap-1">
            <Button
              variant="outline"
              size="sm"
              onClick={() => table.setPageIndex(0)}
              disabled={!table.getCanPreviousPage()}
              aria-label={LABELS.goFirst}
              title={LABELS.goFirst}
            >
              <IconChevronsLeft className="size-4" />
            </Button>
            <Button
              variant="outline"
              size="sm"
              onClick={() => table.previousPage()}
              disabled={!table.getCanPreviousPage()}
              aria-label={LABELS.goPrev}
              title={LABELS.goPrev}
            >
              <IconChevronLeft className="size-4" />
            </Button>
            <span className="px-2 text-sm font-medium" aria-live="polite">
              {LABELS.page} {numberFormatter.format(currentPage)} {LABELS.of} {numberFormatter.format(totalPages)}
            </span>
            <Button
              variant="outline"
              size="sm"
              onClick={() => table.nextPage()}
              disabled={!table.getCanNextPage()}
              aria-label={LABELS.goNext}
              title={LABELS.goNext}
            >
              <IconChevronRight className="size-4" />
            </Button>
            <Button
              variant="outline"
              size="sm"
              onClick={() => table.setPageIndex(totalPages - 1)}
              disabled={!table.getCanNextPage()}
              aria-label={LABELS.goLast}
              title={LABELS.goLast}
            >
              <IconChevronsRight className="size-4" />
            </Button>
          </div>

          <label className="flex items-center gap-2 text-sm">
            <span className="text-xs text-muted-foreground">{LABELS.rowsPerPage}</span>
            <select
              value={pagination.pageSize}
              onChange={(event) => table.setPageSize(Number(event.target.value))}
              className="h-8 rounded-md border border-input bg-background px-2 text-sm text-foreground shadow-sm focus:outline-none focus:ring-2 focus:ring-ring/50"
              aria-label={LABELS.rowsPerPage}
              title={LABELS.rowsPerPage}
            >
              {[15, 25, 30, 40, 50].map((size) => (
                <option key={size} value={size}>
                  {size}
                </option>
              ))}
            </select>
          </label>
        </div>
      </div>
    </section>
  )
}
